<template>
	<div class="OrderDetails">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span></span></header>
		<div class="container">
			<div class='WaybillInfo'>
			    <div><span>{{CustomerInfo.name}}</span></div>
			    <div><span>{{CustomerInfo.recAddr1}}</span></div>
			    <div>
					 &nbsp;&nbsp;
					<span>{{CustomerInfo.recName}}&nbsp;&nbsp;&nbsp; {{CustomerInfo.recPhone}}</span>&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;<i @click="callPhone(CustomerInfo.recPhone)" class="iconfont icon-dianhua-copy" style="color: #2B5FD8" ></i>
				</div>
		
		    	<div>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>{{CustomerInfo.shippedVol}} &nbsp;&nbsp;&nbsp; </span><span>{{CustomerInfo.shippedWt}} &nbsp;&nbsp;&nbsp; </span><span>{{CustomerInfo.shippedQty}}</span></div>
			    <div><span>{{CustomerInfo.phoneAppointTime}}</span></div>
		
			</div>
			<div class="orderHead" ></div>
			<div class='goodsInfo'   v-for='(dataItem,index) in deliveryList'  :id="index"  :key='index'  @click="selectData(index,dataItem.ifDeliver)">
			    <div class='goodsHead'><span v-if="dataItem.ifDeliver" class="iconfont icon-xuanzhong"></span><span class="commodityInfo"></span><span class="OrderNum">{{dataItem.omsNo}}</span></div>
			    <div class='goodsContent'>
				    <div   v-for='(Item,idx) in dataItem.deliveryDetailList'  :id="idx"  :key='idx'>{{Item.productNo}} &nbsp;&nbsp;&nbsp;  {{Item.productName}}&nbsp;&nbsp;&nbsp;      {{Item.poOrderQty}}{{Item.poUom}}</div>
			    </div>
			    <div class="goodsBtn">
			    	<span v-if="dataItem.ifDeliver" class="g_btn" @click="toOrderDeliver(index)"></span>
			    	<span v-else class="g_btn" style="background-color: #d5d5d5;"></span>
			    </div>
			</div>
			<div class="g_doubleBtn bottomBtn">
                <div   class="selectAll"  @click="selectAll"></div>
                <div @click="toOrderDeliver('selectAll')"></div>
            </div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import $ from "jquery"
	export default{
		name:"OrderDetails",
		data(){
			return{
				WaybillInfo:[],//vuex
				CustomerInfo:[],// 
				deliveryList:[],// 
			}
		},
		created(){

			var that = this;

			this.WaybillInfo = this.$store.state.WaybillInfo;//vuex

			this.CustomerInfo = this.$store.state.WaybillInfo.detailInfo;// 

			var WaybillInfo = this.$store.state.WaybillInfo;

			var postData = {
				shipId: WaybillInfo.shipmentID,//ID
				orgnizationId: WaybillInfo.detailInfo.orgnizationId,//ID
				receiveAddr: WaybillInfo.detailInfo.recAddr1,//
				tenantCode: WaybillInfo.tenantCode,//
			}
			
			this.httpRequest( "queryDeliverDetailInfo.do",postData,function(res){

				if(res.data.length){

					that.deliveryList = res.data;

					that.$store.state.deliveryList =  res.data;

					// 
					for(var m = 0; m < res.data.length; m++){

						if( res.data[m].status == "" || res.data[m].status == "" ){
							// 
							that.deliveryList[m].ifDeliver = true;

						}else{
							// 
							that.deliveryList[m].ifDeliver = false;
						}
					}
				}else{

					// that.tipsShow();

					// that.msg = "";
					//

				}
			})
		},
		methods:{
			//  
			toOrderDeliver(index){

				event.stopPropagation();

				var that = this;

				var deliverNo_list = [];

				// 
				if(index != "selectAll"){

					deliverNo_list[0] = that.deliveryList[index].deliveryId;

					that.$router.push({
						name:"orderDeliver",
						query:{
							deliverNo_list:deliverNo_list
						}
					})

					return;

				// 
				}else{

					for(var i = 0 ; i < that.deliveryList.length;i++){

						if($(".goodsInfo").eq(i).children(".goodsHead").children("span.iconfont").hasClass("icon-xuanzhong")){

							deliverNo_list[deliverNo_list.length] = that.deliveryList[i].deliveryId;
						}
					}

					if(!deliverNo_list.length){

						that.$alert("", '', {
					        confirmButtonText: ''
					   	})

					}else{
						that.$router.push({
							name:"orderDeliver",
							query:{
								deliverNo_list:deliverNo_list
							}
						})
					}
				}

				console.log(deliverNo_list)
			},
			//
			selectData:function(idx,ifDeliver){

				var that = this;

				if(!ifDeliver){
					return;
				}

				var iconElement = $(".goodsInfo").eq(idx).children(".goodsHead").children("span.iconfont");
				
				if(iconElement.hasClass("icon-xuanzhong")){

					iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong")

				}else if(iconElement.hasClass("icon-weixuanzhong")){

					iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
				}

				var if_Element;
				var if_changeBtn = {
					Selected:false,
					unSelected:false
				};
				for(var n = 0; n < that.deliveryList.length; n++){

					if_Element = $(".goodsInfo").eq(n).children(".goodsHead").children("span.iconfont");

					if(if_Element.hasClass("icon-xuanzhong")){

						if_changeBtn.Selected = true;

					}else if(if_Element.hasClass("icon-weixuanzhong")){

						if_changeBtn.unSelected = true;

					}
				}

				if(if_changeBtn.Selected != if_changeBtn.unSelected){

					if($(".selectAll").html() == ""){

						$(".selectAll").html("");

					}else{
						$(".selectAll").html("");
					}
				}
			},
			// 
			selectAll(){

				var that = this;

				if($(".selectAll").html() == ""){

					$(".selectAll").html("");

					var iconElement;

					for(var j = 0; j < that.deliveryList.length; j++){

						iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

						iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong");
					}

				}else{

					$(".selectAll").html("");

					var iconElement;

					for(var j = 0; j < that.deliveryList.length; j++){

						iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

						iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
					}
				}
			}
		}
	}
</script>
<style lang="less" scoped>
	.OrderDetails{
		height: 100%;
		width: 100%;
		.container{
			padding-bottom: 150/50rem;
			width:100%;
		  	font-size: 29/50rem;
		  	overflow: hidden;
			.WaybillInfo{
				padding:0 16/50rem;
				box-sizing: border-box;
				background-color: #fff;
			  	overflow: hidden;
			  	&>div{
				  	overflow: hidden;
				  	height: 60/50rem;
				  	line-height: 60/50rem;
					&>div{
					  	float: left;
					  	width: 375/50rem;
					}
				}
			}
			.orderHead{
				text-align: center;
				height: 65/50rem;
				line-height: 65/50rem;
				color: #000;
				background-color: #EDEFF3;
			}
			.goodsInfo{
				color: #777;
				padding:0 16/50rem;
				box-sizing: border-box;
			  	border-bottom: 8/50rem solid #EDEFF3;
			  	background-color: #fff;
			  	.goodsHead{
			  		overflow: hidden;
			  		height: 70/50rem;
			  		line-height: 70/50rem;
			  		border-bottom: 1/50rem solid #EDEFF3;
			  		span.iconfont{
						color:#285DCE;
						font-size: 40/50rem;
						float: left;
						margin-right: 20/50rem;
			  		}
			  		span.commodityInfo{
			  			float: left;
			  		}
			  		span.OrderNum{
			  			float: right;
			  			color: #999;
			  		}
			  	}
			  	.goodsContent{
			  		&>div{
			  			margin-top: 20/50rem;
			  		}
			  	}
			  	.goodsBtn{
			  		height: 80/50rem;
			  		margin-top: 20/50rem;
					span{
						float: right;
						height:60/50rem;
						line-height: 60/50rem;
						width: 200/50rem;
						
					}
			  	}
			}
			// 
			span.g_btn{
				border-radius:10/50rem;
				color:#fff;
				background-color: #6897FF;
				text-align: center;
			}
			.bottomBtn{
				width: 100%;
				height: 90/50rem;
				position: fixed;
				bottom: 0;
				div{
					border-radius: 35/50rem;
				}
			}
		}
	}
</style>


