<template>
	<div class="ArrivalConfirm">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span></span></header>
		<div class="container borderLine">
			<div>
				<div><span></span><span>{{LicensePlateNum}}</span></div>
				<div><span></span><span>{{OddNum}}</span></div>
			</div>
			<div><span></span><div class="nowDate">{{nowDate}}</div></div>
			<div><span></span><span>{{CurrentLocation}}</span></div>
			<div class="uploadImg CarPhoto">
				<div><span>(3)</span></div>
				<el-upload  action="http://120.237.87.102:8081/tmsApp/uploadPicture.do" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="CarUpload" :limit="3" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</div>
			<div class="uploadImg  StationPhoto">
				<div><span>(3)</span></div>
				<el-upload action="http://120.237.87.102:8081/tmsApp/uploadPicture.do" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="StationUpload"  :limit="3" :multiple="true"  :before-upload="beforeUpload"  :on-preview="handlePictureCardPreview" :on-error="uploadError" :on-success="uploadSuccess">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<!--  -->
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</div>
		</div>
		<div class="submitBtn">
			<p class="g_signBtn"  @click="submit"></p>
		</div>
		<div v-if="ifTips" class="msg_tips">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
	</div>
</template>
<script type="text/javascript">

	// 
	import {BaiduMap} from 'vue-baidu-map'
	import $ from "jquery"
	import lrz from 'lrz'

	export default{
		name:"ArrivalConfirm",
		data(){
			return{
				dataList:[],//
				LicensePlateNum:"",//
				OddNum:"",//
				nowDate:"",//
				CurrentLocation:"",//
				dialogImageUrl: '',//
		        dialogVisible: false,//
		        tips_Msg:"",//
				ifTips:false,//
				uploadParams:{},// 
			}
		},
		mounted(){

			if(this.$store.state.Device == "iOS") {

				this.CurrentLocation = this.Geolocation('e');
			}else {

				this.TelliOSORAndroidVueMounted("");
				window.SetCurrAddress = this.SetCurrAddress;
			}
		},
		created(){

			if(this.$route.query.StowageList){

				this.dataList = this.$route.query.StowageList;
			}

			this.uploadParams.tenantCode = this.dataList.tenantCode;//

			this.uploadParams.code = this.dataList.shipmentCode;//()

			this.uploadParams.codeType = "SHIPMENT";// 

			this.uploadParams.codeId = this.dataList.shipmentID;//

			this.LicensePlateNum = this.dataList.vehicleCode;//

			this.OddNum = this.dataList.shipmentCode;//
			
			this.nowDate = this.getNowTime();
		},
		methods:{
			SetCurrAddress:function(address) {

				if(address == "") {

					this.$alert(msg, '', {
	            		confirmButtonText: '',
	            		callback: action => {
	            		}
           			})
				}else {

					this.CurrentLocation = address;
				}
			},
			submit(){

				var that = this;

				if(!$(".CarPhoto ul>li").length){

					that.$alert('', '', {
			            confirmButtonText: ''
		           	})

		           	return;
				}else if(!$(".StationPhoto ul>li").length){

					that.$alert('', '', {
			            confirmButtonText: ''
		           	})

		           	return;
				}

				this.$refs.StationUpload.submit();
				this.$refs.CarUpload.submit();

				console.log(this.uploadParams)

				var postData = {
					shipmentId : this.dataList.shipmentID,//ID
					tenantCode : this.dataList.tenantCode,//
				    daoDaTime : this.nowDate,//  2018-08-06 18:56:43
				    daoDaPeople: that.$store.state.userInfo.userName,//
				    daoDaPlace: this.CurrentLocation,//
				}

				this.httpRequest( "arriveConfirmation.do",postData,function(res){

					that.ifTips = true;
					that.tips_Msg = "";

					setTimeout(function(){

						that.ifTips = false;
						that.toWaybill();
					},1500)
				})
			},
			// 
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			}
		}
	}
</script>
<style lang="less" scoped>
	.ArrivalConfirm{
		width: 100%;
		height: 100%;
		.container{
			overflow: hidden;
			padding-bottom: 150/50rem;
			&>div{
				width: 100%;
				padding: 20/50rem;
				height: auto;
				line-height: 55/50rem;
				box-sizing: border-box;
				position: relative;
				overflow: hidden;
				&:nth-child(1){
					&>div{
						float: left;
						width:335/50rem;
						&:nth-child(2){
							width: 375/50rem;
						}
					}
				}
				&:nth-child(3){
					span{
						float:none;
					}
				}
				span{
					float:left;
					height: 50/50rem;
					line-height: 50/50rem;
				}
				.nowDate{
					float: left;
					height: 50/50rem;
					line-height: 50/50rem;
				}
			}
			&>div.uploadImg{
				div{
					float: none;
				}
				span{
					float: none;
					color: #aaa;
				}
			}
		}
	}
</style>


