<template>
    <div class="Search">
	    <header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i<span></span></header>
		<div class="container">
  			<div class='CompetitionScreen'>
    			<div class='screen'>
      			    <div><span></span><el-date-picker id="startvalue" v-model="startvalue"  type="date"  placeholder=""></el-date-picker></div>
      			    <div><span></span><el-date-picker id="endValue" v-model="endValue"  type="date"  placeholder=""></el-date-picker></div>

		              <!--  -->
      			    <div v-if="CostStatisticsHide"   @click='typeShow'><span></span><div><span>{{Cost_Settlement}}</span><i class="iconfont icon-msnui-menu-right"></i></div></div>
      			    <div v-if="CostStatisticsHide" ><span></span><input v-model='Cost_transAgreement' placeholder='' ></input></div>
      			    <div v-if="CostStatisticsHide"  ><span></span><input  v-model='Cost_WaybillNum' placeholder='' ></input></div>

      			    <!--  -->
      			 <!--    <div v-if="orderHide"  id="orderStatus"  @click='typeShow'><span></span><div><span>{{orderStatus_stateText}}</span><i class="iconfont icon-msnui-menu-right"></i></div></div>
      			    <div v-if="orderHide"  id="balanceStatus"   @click='typeShow'><span></span><div><span>{{balanceStatus_stateText}}</span><i class="iconfont icon-msnui-menu-right"></i></div></div>
      			    <div v-if="orderHide"   id="businessType"   @click='typeShow'><span></span><div><span>{{businessType_stateText}}</span><i class="iconfont icon-msnui-menu-right"></i></div></div> -->
      			    <div>
                        <span></span>
                        <input v-model='order_DeliveryNum' placeholder=''  ></input>
                        <i v-if="order_DeliveryNum" @click="clearInput('order_DeliveryNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
      			    <div>
                        <span></span>
                        <input v-model='order_WaybillNum' placeholder=''  ></input>
                        <i v-if="order_WaybillNum" @click="clearInput('order_WaybillNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
                    <div>
                        <span></span>
                        <input v-model='CustomerNum' placeholder=''  ></input>
                        <i v-if="CustomerNum" @click="clearInput('CustomerNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
                    
<!-- v-if="orderHide" -->
<!-- v-if="orderHide" -->
		              <!--  -->
      			    <div  v-if="findGoodsHide"   @click='typeShow'><span></span><div><span>{{Cost_Settlement}}</span><i class="iconfont icon-msnui-menu-right"></i></div></div>

		              <!--  -->
      			    <div v-if="grabGoodsHide" @click='typeShow'><span></span><div><span>{{Cost_Settlement}}</span><i class="iconfont icon-msnui-menu-right"></i></div></div>

		        </div>
                <div class="g_doubleBtn">
                    <div @click="reset"></div>
                    <div @click="sureScreen"></div>
                </div>
		    </div>
	    </div>
	</div>
</template>
<script type="text/javascript">
    
    import $ from "jquery"
	export default{
		name:"Search",
		data(){
			return{
                startvalue:null,//
                endValue:null,//
			    //
			    typeHidden:false,//

                // 
                order_DeliveryNum:null,//
                order_WaybillNum: null,//
                CustomerNum:null,//

			    CostStatisticsHide:false,// 
			    orderHide: false,// 
			    findGoodsHide: false,// 
			    grabGoodsHide: false,// 

			    Cost_Settlement:"",//  
			    orderStatus_stateText: "",//  
			    balanceStatus_stateText: "",//  
			    businessType_stateText:"",//  
			    thisPage:'',//

			    Cost_transAgreement: null,// 
			    Cost_WaybillNum: null,//  
			}
		},
    	methods:{

            //   clientOrderNo
    		//   
            sureScreen(){

                var that = this;

                var startDate,endDate;

                if($("#startvalue").val()){

                    startDate = $("#startvalue").val();

                }else{
                    startDate =null;
                }

                if($("#endValue").val()){

                    endDate = $("#endValue").val();

                }else{
                    endDate =null;
                }

                var postData = {
                    startDate: startDate,// 
                    endDate: endDate,//  
                    deliveryNoinput: this.order_DeliveryNum,// 
                    shipmentCodeinput: this.order_WaybillNum,//   
                    clientOrderNo: this.CustomerNum,//
                    cellphone: that.$store.state.userInfo.cellphone,//
                }

                this.httpRequest( "queryShipmentData.do",postData,function(res){

                    that.bigListData = res.data;
                    
                    that.$store.state.WaybillData = res.data;
                    
                    that.$store.state.NoSearchFrom = false;// false

                    that.$router.push({
                        name:"Waybill",
                        query:{
                           
                        }
                    })
                })
            },
            // 
            reset(){
                this.startvalue = null;//
                this.endValue = null;//

                // 
                this.order_DeliveryNum = null;//
                this.order_WaybillNum =  null;//
                this.CustomerNum = null;//

                this.Cost_transAgreement =  null;// 
                this.Cost_WaybillNum =  null;//  

                this.orderStatus_stateText = "";// 
                this.balanceStatus_stateText =  "";//  
                this.businessType_stateText =  "";//  
                this.Cost_Settlement =  "";//  

                this.empty = "";//input
                this.Cost_SettlementId = null;
                this.orderStatus = null;
                this.balanceStatus = null;
                this.businessType = null;
          
            },

            //  
            typeShow(e){

                var that = this;

                // 
                if (that.thisPage =="CostStatistics"){

                    // that.typeOfCarList = getApp().globalData.StateOfSettlement,//
                    that.selectiveType = "";// 

                //
                } else if (that.thisPage == "grabGoods"){

                    // that.typeOfCarList = getApp().globalData.BiddingState;
                    that.selectiveType = "";
          
                //
                } else if (that.thisPage == "findGoods") {

                    // that.typeOfCarList = getApp().globalData.findState;
                    that.selectiveType = "";

                //
                } else if (that.thisPage == "order") {

                    //
                    if (e.currentTarget.id == "orderStatus"){

                        // var typeOfCarList = getApp().globalData.orderStatus;
                        var selectiveType = "";

                        // that.orderStatus = getApp().globalData.orderStatus;
                        that.typeShow_detail = "orderStatus";

                    //
                    } else if (e.currentTarget.id == "balanceStatus") {

                        // var typeOfCarList = getApp().globalData.balanceStatus;
                        var selectiveType = "";

                        // that.balanceStatus = getApp().globalData.balanceStatus;
                        that.typeShow_detail = "balanceStatus";

                    //
                    } else if (e.currentTarget.id == "businessType") {

                        var typeOfCarList = [
                          {
                            state: "",
                            code: "PICKUP"
                          },
                          {
                            state: "",
                            code: "MAINLINE"
                          }
                        ];
                        var selectiveType = "";

                        that.businessType = typeOfCarList;
                        that.typeShow_detail = "businessType";

                    }

                  	that.typeOfCarList = typeOfCarList;
                    that.selectiveType = selectiveType;
                   
                } 

                this.typeHidden = true;
            },
            //   
            typeSure (e){

                var that = this;

                //
                if (that.thisPage == "order"){

                    //
                    if (that.typeShow_detail =="orderStatus"){

                      	that.orderStatus_state = that.orderStatus[e.currentTarget.id];// 
                        that.orderStatus_stateText = that.orderStatus[e.currentTarget.id].state;//  


                    //
                    } else if (that.typeShow_detail == "balanceStatus") {

                
                        that.balanceStatus_state = that.balanceStatus[e.currentTarget.id];// 
                        that.balanceStatus_stateText = that.balanceStatus[e.currentTarget.id].state;//  


                    //
                    } else if (that.typeShow_detail == "businessType") {

                
                        that.businessType_state = that.businessType[e.currentTarget.id];// 
                        that.businessType_stateText = that.businessType[e.currentTarget.id].state;//  
                     
                    }

                }else{

                  that.Cost_Settlement = that.typeOfCarList[e.currentTarget.id].state;//  

                }

              	that.typeHidden = false;
                that.ost_SettlementId = e.currentTarget.id;//id

            },
            //  
            typeCancle(){
                this.typeHidden = false;

            },
    	}	
    }
</script>
<style lang="less" scoped>
  	.container{
    	.CompetitionScreen{
            height: 100%;
            font-size: 28/50rem;
            &>.screen{
                background-color: #fff;
                &>div{
                    border-bottom: 1/50rem solid #EDEDED;
                    padding:0 20/50rem;
                    box-sizing: border-box;
                    position: relative;
                    height: 95/50rem;
                    line-height: 95/50rem;
                    &>span{
                        display: block;
                        margin-right: 30/50rem;
                        float: left;
                    }
                    &>input{
                        float: left;
                        border: none;
                        width: 450/50rem;
                        height: 93/50rem;
                    }
                    .icon-iconfontcuowu{
                        right:50/50rem;
                        top: 4/50rem;
                    }
                }
            }
        }
  	}
</style>


