import Vue from 'vue'
import Router from 'vue-router'
// 
import Login from '@/pages/Login/Login'  //
import Reg from '@/pages/Login/reg'  //
import ForgotPwd from '@/pages/Login/forgotPwd'  //
import Identifying from '@/pages/Login/identifying'  //


// 
import Binding from "@/pages/Login/Binding" // openid

// 
import Search from '@/pages/search'

//
import Waybill from '@/pages/Waybill/Waybill'  //
import LoadingAppoint from '@/pages/Waybill/LoadingAppoint'  // 
import UnloadAppoint from '@/pages/Waybill/UnloadAppoint'  // 
import WayFeedback from '@/pages/Waybill/WayFeedback'  //
import DepartureConfirm from '@/pages/Waybill/DepartureConfirm'  //
import ArrivalConfirm from '@/pages/Waybill/ArrivalConfirm'  //
import WaybillDetail from '@/pages/Waybill/WaybillDetail/WaybillDetail'  //

//  
import OrderDetails from '@/pages/Waybill/Deliver/OrderDetails'  //
import orderDeliver from '@/pages/Waybill/Deliver/orderDeliver'  //

// 
import FindGoods from '@/pages/findGoods/findGoods'  //

// 
import GrabGoods from '@/pages/grabGoods/grabGoods'  //

// 
import HomeIndex from '@/pages/home/home'  //



Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    // 
    {
      path: '/Binding',
      name: 'Binding',
      component: Binding
    },
    {
      path: '/Reg',
      name: 'Reg',
      component: Reg
    },
    {
      path: '/ForgotPwd',
      name: 'ForgotPwd',
      component: ForgotPwd
    },
    {
      path: '/Identifying',
      name: 'Identifying',
      component: Identifying
    },
    // 
    {
      path: '/Search',
      name: 'Search',
      component: Search
    },
    // 
    {
      path: '/Waybill',
      name: 'Waybill',
      component: Waybill
    },
    {
      path: '/LoadingAppoint',
      name: 'LoadingAppoint',
      component: LoadingAppoint
    },
    {
      path: '/UnloadAppoint',
      name: 'UnloadAppoint',
      component: UnloadAppoint
    },
    {
      path: '/WayFeedback',
      name: 'WayFeedback',
      component: WayFeedback
    },
    {
      path: '/DepartureConfirm',
      name: 'DepartureConfirm',
      component: DepartureConfirm
    },
    {
      path: '/ArrivalConfirm',
      name: 'ArrivalConfirm',
      component: ArrivalConfirm
    },
    {
      path: '/WaybillDetail',
      name: 'WaybillDetail',
      component: WaybillDetail
    },
    //  
    {
      path: '/OrderDetails',
      name: 'OrderDetails',
      component: OrderDetails
    },
    {
      path: '/orderDeliver',
      name: 'orderDeliver',
      component: orderDeliver
    },
    // 
    {
      path: '/FindGoods',
      name: 'FindGoods',
      component: FindGoods
    },
    // 
    {
      path: '/GrabGoods',
      name: 'GrabGoods',
      component: GrabGoods
    },
    // 
    {
      path: '/HomeIndex',
      name: 'HomeIndex',
      component: HomeIndex
    },
  ]
})



