<template>
	<div class="Reg">
		<header><i class="iconfont icon-xiangzuo1"  @click="goPrev"></i><span></span></header>
		<div class="container">
			<div class='regContainer'>
			  <div class='regInfo'>
			    <div><span></span><input  readonly  v-model='cellphone'></input></div>
			    <div><span></span><input placeholder=''   ref="userName"   @keyup.enter="getFocus('password')"     v-model='userName'></input><i v-if="userName" @click="clearInput('userName')" class="iconfont icon-iconfontcuowu"></i></div>
			    <div><span></span><input placeholder=''   ref="password"   @keyup.enter="getFocus('vicheNo')"    v-model='password'></input><i v-if="password" @click="clearInput('password')" class="iconfont icon-iconfontcuowu"></i></div>
			    <div><span></span><input placeholder=''   ref="vicheNo"   @keyup.enter="getFocus('vehicleLength')"    v-model='vicheNo'></input><i v-if="vicheNo" @click="clearInput('vicheNo')" class="iconfont icon-iconfontcuowu"></i></div>

			    <div @click='TypeOfCar'><span></span><div><span>{{vehicleType}}</span><i class="iconfont icon-xiangshang"></i></div></div>
			    <div><span></span><input placeholder=''   ref="vehicleLength"   @keyup.enter="getFocus('vehicleWide')"    v-model='vehicleLength'></input><i v-if="vehicleLength" @click="clearInput('vehicleLength')" class="iconfont icon-iconfontcuowu"></i></div>
			    <div><span></span><input placeholder=''   ref="vehicleWide"   @keyup.enter="getFocus('vehicleHeight')"    v-model='vehicleWide'></input><i v-if="vehicleWide" @click="clearInput('vehicleWide')" class="iconfont icon-iconfontcuowu"></i></div>
			    <div><span></span><input placeholder=''   ref="vehicleHeight"   @keyup.enter="getFocus('maxLoadWeight')"    v-model='vehicleHeight'></input><i v-if="vehicleHeight" @click="clearInput('vehicleHeight')" class="iconfont icon-iconfontcuowu"></i></div>
			    <div><span></span><input placeholder=''   ref="maxLoadWeight"   @keyup.enter="getFocus('maxLoadVolumn')"    v-model='maxLoadWeight'></input><i v-if="maxLoadWeight" @click="clearInput('maxLoadWeight')" class="iconfont icon-iconfontcuowu"></i></div>
			    <div><span></span><input placeholder=''   ref="maxLoadVolumn"   @keyup.enter="regBtn"    v-model='maxLoadVolumn'></input><i v-if="maxLoadVolumn" @click="clearInput('maxLoadVolumn')" class="iconfont icon-iconfontcuowu"></i></div>
			  </div>
			  <div class="g_signBtn" @click='regBtn'></div>
			</div>
			<div v-if="ifTips" class="msg_tips">
				<i class="iconfont icon-chenggong1"></i>
				<p>{{tips_Msg}}</p>
			</div>
			<div class="SelectMeng"   v-if="typeMShow">
				<div class="SelectWarehouses" >
					<div class="SelectTitle"></div>
					<div class="SelectItem">
						<div @click="SelectComit(index)"  v-for='(dataItem,index) in typeOfCarList'  :id="index"  :key='index'>{{dataItem.description}}</div>
					</div>
					<div class="cancel"   @click="selectCancel('e')"></div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Reg",
		data(){
			return{
				typeMShow:false,
				cellphone:"",//
				userName:"",//
				password:"",//
				vicheNo:"",//
				vehicleType:"",//
				vehicleType_code:"",//
				vehicleLength:"",//
				vehicleWide:"",//
				vehicleHeight:"",//
				maxLoadWeight:'',//
				maxLoadVolumn:"",//
				tips_Msg:"",//
				ifTips:false,//
				typeOfCarList:[],//

			}
		},
		mounted(){

			this.$refs['userName'].focus();
		},
		created(){

			var that = this;

			this.cellphone = this.$route.query.phoneNum;

		    if (this.$store.state.typeOfCarList.length) {

		        this.typeOfCarList = that.$store.state.typeOfCarList;

		    } else {

		        var requestData = { listName: "TMS-" };

	            this.httpRequest( "queryCodeLukupData.do",requestData,function(res){

	            	that.typeOfCarList = res.data;
				    that.$store.state.typeOfCarList = res.data;
	            })
		    }
		},
		methods:{
			goPrev(){
				this.$router.push({
					name:"Identifying",
					query:{
						page:"Reg"
					}
				})
			},
			 //   
			regBtn (){

			    var that = this;

			    if(!(that.userName && that.password && that.vicheNo && that.maxLoadWeight && that.maxLoadVolumn)){

			    	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return ;

			    }else if(!that.vehicleType_code){

			    	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return ;
			    }else if (that.password.length<6){

			      	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return;

			    }else{

			      	var params = {
			          loginname: "",//
			          username: that.userName,//
			          password: that.password,//
			          cellphone: that.cellphone,//
			          vicheNo: that.vicheNo,//
			          vehicleType: that.vehicleType_code,//
			          vehicleLength: that.vehicleLength,//
			          vehicleWide: that.vehicleWide,//
			          vehicleHeight: that.vehicleHeight,//
			          maxLoadWeight: that.maxLoadWeight,//
			          maxLoadVolumn: that.maxLoadVolumn,//
			        };

			        that.httpRequest( "registerAppUser.do",params,function(res){

			        	that.ifTips = true;

						that.tips_Msg = res.Msg;

						setTimeout(function(){

							that.ifTips = false;
							
							that.toLogin();
						},1500)
			        })
			    }
			},
			//  
			TypeOfCar (){

			    this.typeMShow = true;
			},
			//    
			SelectComit:function(index){
				this.typeMShow = false;
			    this.vehicleType = this.typeOfCarList[index].description;
			    this.vehicleType_code = this.typeOfCarList[index].code;
			},
		}
	}
</script>
<style lang="less" scoped>
	.Reg{
		.regContainer{
			&>.regInfo{
				&>div{
					position: relative;
				    width:100%;
				    padding: 20/50rem 30/50rem;
				    box-sizing: border-box;
				    overflow: hidden;
				    font-size: 28/50rem;
				    border-bottom: 1/50rem solid #EDEDED;
				    &>span{
					    float: left;
					    width: 120/50rem;
					    height: 50/50rem;
					    line-height: 50/50rem;
					}
					&>input{
						width: 500/50rem;
					    float: left;
					    border: none;
					}
					&>div{
					    float: right;
					    color: #7A7A7A;
					    line-height: 50/50rem;
					    i{
					    	margin-left: 10/50rem;
					    }
					}
				}
			}
			&>button{
			    width:690/50rem;
			    color: #fff;
			    background-color: #ff681d;
			    font-size: 28/50rem;
			    margin-top: 50/50rem;
			}
		}
	}
</style>


