<template>
	<div class="LoadingAppoint">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span></span></header>
		<div class="container borderLine">
			<div>
				<div><span></span><span>{{LicensePlateNum}}</span></div>
				<div><span></span><span>{{OddNum}}</span></div>
			</div>
			<div v-if="!IFselect"><span></span><span>{{PlaceLoading}}</span></div>
			<div class="place"   v-else  @click="typeSelect('e')">
				<div><span></span><span>{{PlaceLoading}}</span></div>
				<i class="iconfont icon-xiangshang"></i>
			</div>
			<div><span></span><el-date-picker size="small" id="datetime" v-model="AppointmentTime"  :time-arrow-control=true  type="datetime"  placeholder=""></el-date-picker></div>
			<p class="g_signBtn" @click="submit"></p>
		</div>
		<div v-if="ifTips" class="msg_tips">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
		<div class="Navigate_MultiAddress" v-if="typeMShow">
			<div>
				<div class="SelectTitle"></div>
				<div class="SelectContent">
					<div class="addressContent" @click="SelectComit(itemAddress.issueAddr1)"  v-for='(itemAddress,idx) in address_listData'  :id="idx"  :key='idx'>
						<div>
							<span>{{itemAddress.issueAddr1}}</span>
						</div>
						<div>
							<span>{{itemAddress.issueUser}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span>{{itemAddress.issuePhone}}</span><i style="color:#2B5FD8;" @click="callPhone(itemAddress.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
						</div>
					</div>
				</div>
				<div class="cancel"   @click="selectCancel('e')"></div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">

	// 
	import BMap from 'BMap'
	import $ from "jquery"
	import Axios from "axios"

	export default{
		name:"LoadingAppoint",
		data(){
			return{
				dataList:[],//
				LicensePlateNum:"",//
				OddNum:"",//
				PlaceLoading:"",//
				AppointmentTime:"",//
				tips_Msg:"",//
				ifTips:false,//
				address_listData:[],//
				typeMShow:true,//
				IFselect:false,//
				Exceed:false,//
			}
		},
		mounted(){

		},
		created(){

			var that = this;

			if(this.$route.query.StowageList){

				this.dataList = this.$route.query.StowageList;
			}

			this.LicensePlateNum = this.dataList.vehicleCode;//
			this.OddNum = this.dataList.shipmentCode;//

			if(this.dataList.issueAddrLiat.length <= 1){

				this.PlaceLoading = this.dataList.issueAddrLiat[0].issueAddr1;//

				this.IFselect = false;
				this.typeMShow = false;
			}else{

				this.IFselect = true;
				this.typeMShow = true;
			}
			
			this.address_listData = this.dataList.issueAddrLiat;//

		},
		methods:{
			submit:function(){

				var that = this;

				if(!this.PlaceLoading){
					//
					that.$alert('', '', {
			            confirmButtonText: '',
			            callback: action => {
				            
			            }
		           	})
					return;

				}else if(!$("#datetime").val()){
					//
					that.$alert('', '', {
			            confirmButtonText: '',
			            callback: action => {
				            
			            }
		           	})
					return;
				}

				this.timeCompare(this.getNowTime(),$("#datetime").val(),30);

				if(this.Exceed){

					that.$alert(',30', '', {
			            confirmButtonText: ''
		           	})
		           	this.Exceed = false;
					return;
				}

				var postData = {
				    shipmentId :  this.dataList.shipmentID,//ID
				    yuYueTime : $("#datetime").val(),//  2018-07-24 2:09:12
				    tenantCode: this.dataList.tenantCode,//
				    appointLoadingSite:this.PlaceLoading,//
			    }

				this.httpRequest( "loadingReservation.do",postData,function(res){

					that.ifTips = true;
					that.tips_Msg = "";

					setTimeout(function(){

						that.ifTips = false;
						that.toWaybill();
					},1500)
				})
			},
			// 
			SelectComit(detailPlace){

				this.typeMShow = false;
				this.PlaceLoading = detailPlace ;
			}
		}
	}
</script>
<style lang="less" scoped>
	.LoadingAppoint{
		overflow: hidden;
		height: 100%;
	}
	.container{
		padding-top: 1/50rem;
		&>div{
			padding: 20/50rem;
			box-sizing: border-box;
			overflow: hidden;
			&:nth-child(1){
				&>div{
					float: left;
					width: 335/50rem;
					overflow: hidden;
					&:nth-child(2){
						width: 375/50rem;
					}
				}
			}
			&:nth-child(2){
				overflow: hidden;
				position: relative;
				div{
					width: 650/50rem;
				}
				span{
					float: left;
			    	height: auto;
			    	width:150/50rem;
			    	&:nth-child(2){
						width: 500/50rem;
			    	}
			    }
			    i{
			    	position: absolute;
			    	top: 50%;
			    	margin-top: -20/50rem;
			    	right: 25/50rem;
			    	line-height:  50/50rem;
			    	color: #7A7A7A;
			    }
			}
			span{
				float: left;
				height: 50/50rem;
				line-height: 50/50rem;
			}
		}
	}
</style>


