<template>
	<div class="UnloadAppoint">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span></span></header>
		<div class="container borderLine">
			<div><span></span><span>{{LicensePlateNum}}</span></div>
			<div>
				<div><span>  &nbsp;</span><span>{{AppointPerson}}    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  {{AppointTele}}   </span></div>
				
			</div>
			<div><span></span><span>{{PlaceLoading}}</span></div>
			<div><span></span><el-date-picker size="small" id="datetime" v-model="AppointmentTime"  :time-arrow-control=true  type="datetime"  placeholder=""></el-date-picker></div>
			<p class="g_signBtn" @click="submit"></p>
		</div>
		<div v-if="ifTips" class="msg_tips">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
	</div>
</template>
<script type="text/javascript">

	// 
	import BMap from 'BMap'
	import $ from "jquery"
	import Axios from "axios"

	export default{
		name:"UnloadAppoint",
		data(){
			return{
				dataList:[],//
				LicensePlateNum:"",//
				PlaceLoading:"",//
				AppointmentTime:"",//
				tips_Msg:"",//
				ifTips:false,//
				Exceed:false,//
				AppointPerson:"",//
				AppointTele:"",//
			}
		},
		mounted(){

		},
		created(){

			//  
			this.AppointPerson = this.$store.state.userInfo.userName; //
			this.AppointTele = this.$store.state.userInfo.cellphone;//

			this.LicensePlateNum = this.$route.query.vehicleCode;//

			this.PlaceLoading = this.$route.query.recAddr1;//
		},
		methods:{
			submit:function(){

				var that = this;

				if(!$("#datetime").val()){
					//
					that.$alert('', '', {
			            confirmButtonText: '',
			            callback: action => {
				            
			            }
		           	})
					return;
				}

				this.timeCompare(this.getNowTime(),$("#datetime").val(),30);

				if(this.Exceed){

					that.$alert(',30', '', {
			            confirmButtonText: ''
		           	})
		           	this.Exceed = false;
					return;
				}

				var postData = {
				    shipId :  this.$route.query.shipmentID,//ID
				    orgnizationId: this.$route.query.orgnizationId,//ID
				    yuYueTime : $("#datetime").val(),//  2018-07-24 2:09:12
				    tenantCode: this.$route.query.tenantCode,//
				    receiveAddr:this.PlaceLoading,//
			    }

				this.httpRequest( "unloadReservation.do",postData,function(res){

					that.ifTips = true;
					that.tips_Msg = "";

					setTimeout(function(){

						that.ifTips = false;
						that.toWaybill();
					},1500)
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.UnloadAppoint{
		overflow: hidden;
		height: 100%;
	}
	.container{
		padding-top: 1/50rem;
		&>div{
			padding: 20/50rem;
			box-sizing: border-box;
			overflow: hidden;
			&:nth-child(1){
				&>div{
					float: left;
					width: 335/50rem;
					overflow: hidden;
					&:nth-child(2){
						width: 375/50rem;
					}
				}
			}
			&:nth-child(2){
				overflow: hidden;
				position: relative;
				div{
					width: 650/50rem;
				}
				span{
					float: left;
			    	height: auto;
			    	width:150/50rem;
			    	&:nth-child(2){
						width: 500/50rem;
			    	}
			    }
			    i{
			    	position: absolute;
			    	top: 50%;
			    	margin-top: -20/50rem;
			    	right: 25/50rem;
			    	line-height:  50/50rem;
			    	color: #7A7A7A;
			    }
			}
			span{
				float: left;
				height: 50/50rem;
				line-height: 50/50rem;
			}
		}
	}
</style>


