<template>
	<div class="WayFeedback">
		<header><i class="iconfont icon-xiangzuo1"  @click="toWaybill"></i><span></span></header>
		<div class="container borderLine">
			<div >
				<div><span></span><span>{{LicensePlateNum}}</span></div>
				<div><span></span><span>{{OddNum}}</span></div>
			</div>
			<div><span></span><div class="nowDate">{{nowDate}}</div></div>
			<div><span></span><span>{{CurrentLocation}}</span></div>
			<div @click="typeSelect('e')">
				<span></span>
				<div class="PositionReport"><span>{{type}}</span><i class="iconfont icon-xiangshang"></i></div>
			</div>
			<div class="uploadImg" v-if="type==''">
				<div><span>(6)</span></div>
				<el-upload action="http://120.237.87.102:8081/tmsApp/uploadPicture.do" :auto-upload="false"  :data="uploadParams"  list-type="picture-card" :on-preview="handlePictureCardPreview"  ref="upload_ref"   :multiple="true" :limit="6"  :before-upload="beforeUpload"   :on-error="uploadError" :on-success="uploadSuccess">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</div>
			<div><span></span><textarea  v-model="Remark"></textarea></div>
		</div>
		<div v-if="ifTips" class="msg_tips">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
		<div class="submitBtn">
			<p class="g_signBtn"  @click="submit"></p>
		</div>
		<div class="SelectMeng"   v-if="typeMShow">
			<div class="SelectWarehouses" >
				<div class="SelectTitle"></div>
				<div class="SelectItem">
					<div @click="SelectComit('')"></div>
					<div @click="SelectComit('')"></div>
				</div>
				<div class="cancel"   @click="selectCancel('e')"></div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	
	// 
	import {BaiduMap} from 'vue-baidu-map'
	import $ from "jquery"

	export default{
		name:"WayFeedback",
		data(){
			return{
				dataList:[],//
				tips_Msg:"",//
				ifTips:false,//
				LicensePlateNum:"",//
				OddNum:"",//
				CurrentLocation:"",//
				typeMShow:false,//
				type:"",
				dialogImageUrl: '',
		        dialogVisible: false,//
		        nowDate:"",//
		        Remark:"",//
		        uploadParams:{},// 
			}
		},
		mounted(){

			if(this.$store.state.Device == "iOS") {

				this.CurrentLocation = this.Geolocation('e');
			}else {

				this.TelliOSORAndroidVueMounted("");
				window.SetCurrAddress = this.SetCurrAddress;
			}
		},
		created(){

			this.dataList = this.$route.query.StowageList;

			this.LicensePlateNum = this.dataList.vehicleCode;//
			this.OddNum = this.dataList.shipmentCode;//

			this.nowDate = this.getNowTime();

			this.uploadParams.tenantCode = this.dataList.tenantCode;//

			this.uploadParams.code = this.dataList.shipmentCode;//()

			this.uploadParams.codeType = "ABNORMAL";// 

			this.uploadParams.codeId = this.dataList.shipmentID;//
		},
		methods:{
			SetCurrAddress:function(address) {

				if(address == "") {

					this.$alert(msg, '', {
	            		confirmButtonText: '',
	            		callback: action => {
	            		}
           			})
				}else {

					this.CurrentLocation = address;
				}
			},
			submit(){

				var that = this;

				if(this.type == ""){

					if(!$(".uploadImg ul>li").length){

						that.$alert('', '', {
				            confirmButtonText: ''
			           	})

			           	return;
					}else if(!that.Remark){

						that.$alert('', '', {
				            confirmButtonText: ''
			           	})

			           	return;
					}

					this.$refs.upload_ref.submit();
				}

				var postData = {
				    shipmentId : this.dataList.shipmentID,//ID
				    userName : this.$store.state.userInfo.userName,//
				    vehicleLocation : this.CurrentLocation,//
				    trackRemarks : this.Remark,//
				    traceCategory : this.type,//
				    tenantCode: this.dataList.tenantCode,//
			    }

				this.httpRequest( "abnormalInfo.do",postData,function(res){

					that.ifTips = true;
					that.tips_Msg = "";

					setTimeout(function(){

						that.ifTips = false;
						that.toWaybill();
					},1500)
				})
			},
			// 
			SelectComit:function(typeName){
				this.type = typeName;
				this.typeMShow = false;
			},
			// 
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			}
		}
	}
</script>
<style lang="less" scoped>
	.WayFeedback{
		background-color: #fff;
		overflow: hidden;
	}
	.container{
		padding-bottom: 150/50rem;
		&>div{
			position: relative;
			width: 100%;
			padding: 20/50rem;
			box-sizing: border-box;
			overflow: hidden;
			&:nth-child(1){
				&>div{
					float: left;
					width: 335/50rem;
					overflow: hidden;
					&:nth-child(2){
						width: 375/50rem;
					}
				}
			}
			&:nth-child(3){
				span{
					float:none;
				}
			}
			&>div.PositionReport{
				float:left;
				width: 620/50rem;
				height: 50/50rem;
				line-height: 50/50rem;
				text-align: right;
				position: relative;
				&>span{
					float: none;
					margin-right: 60/50rem;
				}
				i{
					font-size: 35/50rem;
					position: absolute;
					color: #aaa;
					top:3/50rem;
					right: 1/50rem;
				}
			}
			span{
				float:left;
				height: 50/50rem;
				line-height: 50/50rem;
			}
			textarea{
				float:left;
				width: 610/50rem;
				height: 230/50rem;
				outline: none;
				border-radius: 5/50rem;
				padding: 10/50rem;
				box-sizing: border-box;
			}
			.nowDate{
				float: left;
				height: 50/50rem;
				line-height: 50/50rem;
			}
		}
		&>div.uploadImg{
			div{
				float: none;
			}
			span{
				float: none;
				color: #aaa;
			}
		}
	}
</style>


